.container.centered {
    display: flex;
    flex-direction: column;
    align-items: center;
}
/*///////////// Materialize CSS Layout ////////////////*/
.container {
    margin: 0 auto;
    max-width: 1280px;
    width: 90%;
}

@media only screen and (min-width: 601px) {
    .container {
        width: 85%;
    }
}

@media only screen and (min-width: 993px) {
    .container {
        width: 70%;
    }
}

.input-field {
    position: relative !important;
    margin-top: 0.7rem !important;
    margin-bottom: 0.5rem !important;
}

.col .row {
    margin-left: -0.75rem;
    margin-right: -0.75rem;
}

.row {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}

    .row:after {
        content: "";
        display: table;
        clear: both;
    }

